@import '../../node_modules/bootstrap/scss/functions';
@import '../../node_modules/bootstrap/scss/variables';



// Custom spacer for margins
$custom-spacers: (
  6:$spacer * 4,
  8:$spacer * 6,
  10 :  $spacer * 15,
  0-1 :  $spacer * 0.01
);
// Merge the maps
$spacers: map-merge($spacers, $custom-spacers);


$gold : #c2a957;
// Custom spacer for margins
$custom-gold: (
  'gold' :  $gold ,
  'search' : $primary,
);
// Merge the maps
$theme-colors: map-merge($theme-colors, $custom-gold);

.btn-gold {
  --bs-btn-font-weight: 600;
  --bs-btn-color: var(--bs-white);
  --bs-btn-bg: var(--gold);
  --bs-btn-border-color: var(--gold);
  --bs-btn-border-radius: .5rem;
  --bs-btn-hover-color: var(--bs-white);
  --bs-btn-hover-bg: #{shade-color($gold, 10%)};
  --bs-btn-hover-border-color: #{shade-color($gold, 10%)};
  --bs-btn-focus-shadow-rgb: var(--gold-rgb);
  --bs-btn-active-color: var(--bs-btn-hover-color);
  --bs-btn-active-bg: #{shade-color($gold, 20%)};
  --bs-btn-active-border-color: #{shade-color($gold, 20%)};
}

.btn-search {
  --bs-btn-font-weight: 600;
  --bs-btn-color: var(--bs-white);
  --bs-btn-bg: var(--gold);
  --bs-btn-border-color: var(--gold);
  --bs-btn-border-radius: 1.5rem !important;
  --bs-btn-hover-color: var(--bs-white);
  --bs-btn-hover-bg: #{shade-color($gold, 10%)};
  --bs-btn-hover-border-color: #{shade-color($gold, 10%)};
  --bs-btn-focus-shadow-rgb: var(--gold-rgb);
  --bs-btn-active-color: var(--bs-btn-hover-color);
  --bs-btn-active-bg: #{shade-color($gold, 20%)};
  --bs-btn-active-border-color: #{shade-color($gold, 20%)};
}

@import "../../node_modules/bootstrap/scss/mixins";

